	onShow() {
		this.oosHost = app.globalData.oosHost;
		uni.startPullDownRefresh();
		common.fonts(); //获取网络字体
		// this.getHotGraphic(); /**获取首页精选图文 */
		// this.indexSwiper(); //获取首页置顶图文

		uni.hideHomeButton(); //隐藏首页按钮

	},
	//停止刷新
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		//获取首页置顶图文
		indexSwiper() {
			common
				.requestURL({
					url: '/graphic/indexTop',
					method: 'GET'
				})
				.then(res => {
					if (res.code == 200) {
						this.bannerUrl = res.data;
					}
				});
		},
		//轮播切换
		change(e) {
			this.current = e.detail.current;
		},
		//页面跳转
		gonewWeb(url) {
			url= encodeURIComponent(url)
			if (url != '') {
				uni.navigateTo({
					url: "/pages/home/web/web?url=" + url
				});
			}
		},

		/**
		 * 获取热门图文-轮播
		 */
		getHotGraphic: function() {
			common
				.requestURL({
					url: '/graphic/hot',
					method: 'GET'
				})
				.then(res => {
					this.graphics = res.data;
				});
		}
	}
};
